body{
  margin:0;
  padding:0;
  background-size:100% 100%;
  background-position: center;
  background-repeat: no-repeat;
  font-family: 'Bangers', cursive;
}
img{
  width:100%;
  height:100%
}
p{
  margin:0;
  padding:0;
}
.game-wrapper{
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.content-wrapper{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction:column;
      flex-direction:column;
  height: 100vh;
}
/* tutorial */
.react-user-tour-container>div{
  bottom:0;
  left:0;
  right:0;
  top:0;
  margin:0 auto;
}
/* intro page render when game has not started */
.page-intro{
  width:100%;
  height:100vh;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.title-img-box{
  width:100%;
  height:530px;
  -webkit-animation:fadeInOut 10s infinite;
          animation:fadeInOut 10s infinite;
}

.title-img{
  width:100%;
  height:100%
}
.title{
  background:red;
  width:100%;
  -ms-flex:1 1;
      flex:1 1;
  font-size: 35px;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  display: -ms-flexbox;
  display: flex;
}
/* end intro page */

.flex-playerbot-row{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  width:100%;
  background: black;
  position: fixed;
  top: 0;
  left: 0;
}
.playerbot-character-box{
  height:210px;
  width: 200px;
  border: 1px solid red;
}
/* play game button */
.btn-danger{
  font-size: 35px;
  font-family: 'Bangers', cursive;
  background:red;
}
.btn-danger:hover{
  -webkit-transition: background-color 0.5s ease;
  -o-transition: background-color 0.5s ease;
  transition: background-color 0.5s ease;
  cursor: pointer;
  background:rgb(190, 186, 186);
}
.btn-danger:active{
  cursor: wait;
}
/* vs button */
.compare-btn {
  position: absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  margin:auto;
  height:10%;
  width:10%;
  font-family: 'Bangers', cursive;
  font-size: 30px;
  background:rgba(255,255,255,0);
  color:white;
  border:1px solid white;
  z-index: 200;
}
.compare-btn:hover{
  -webkit-transition: background-color 1.0s ease;
  -o-transition: background-color 1.0s ease;
  transition: background-color 1.0s ease;
  background:white;
  color:black;
  cursor: pointer;
  z-index:20;
}
/* row and box for top and bottom */
.deck-holder-row{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex:2 1;
      flex:2 1;
}
.deck-holder-box{
 height: 210px;
 width: 200px;
 border: 1px solid red;
 -ms-flex-item-align: center;
     align-self: center;
 position: relative;
}
.deck{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.deck>.next-card:hover{
  border-bottom:3px solid rgba(0, 29, 255, 0.81);
  -webkit-animation:2s ease-in-out;
          animation:2s ease-in-out;
  -webkit-transform:scale(1.1);
      -ms-transform:scale(1.1);
          transform:scale(1.1);

}
.next-card{
  text-shadow: 2px 2px #000000;
  letter-spacing: 2px;
  color:rgba(0, 29, 255, 0.81);
  font-size: 40px;
  border-bottom: 3px solid white;
}

/* game middle area */
.game-middle-row{
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
  margin: 10px 0;
  -ms-flex:1 1;
      flex:1 1
}
.middle-card-boxes{
  /* height: 210px; */
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  border: 1px solid red;
}
.middle-good{
  border: 1px solid blue;
}
.third {
  -webkit-box-shadow: 0px 0px 10px 7px rgb(0, 255, 13), 0px 0px 10px 7px #fcfcfc;
          box-shadow: 0px 0px 10px 7px rgb(0, 255, 13), 0px 0px 10px 7px #fcfcfc;
}
/* end middle area */
.bad-guys{
  overflow: hidden;
}
.deck{
  background:black;
  background-image: url(http://is3.mzstatic.com/image/thumb/Purple122/v4/e1/3d/55/e13d55bd-f063-3a76-6560-6a49c48fb007/source/1200x630bb.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  border-radius: 8px;
}
.deck:hover{
  cursor: pointer;
}
/* winning message on value compare */
.display-message{
  position: absolute;
  top:0;
  bottom:0;
  left:0;
  right:0;
  margin:auto;
  height:50%;
  width:100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  font-size: 60px;
  z-index: 201;
}
.display-message h2{
  z-index: 100;
  animation:colorchange .5s infinite alternate;
  -webkit-animation:colorchange .5s infinite alternate
}
.new-game-box{
  display: inline-block;
  width: 100%;
  text-align: center;
}
.new-game-btn{
  margin-top: 10%;
  padding: 1%;
  background: white;
  font-size: 30px;
  border: none;
  border-radius: 4px;
}
.new-game-btn:hover{
  cursor: pointer;
  background:blue;
  color:white;
}
/* display num cards */
.score-box{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 40px;
}
.score{
  font-size: 25px;
}

@-webkit-keyframes colorchange {
  0% {
    color: rgb(12, 12, 12);
  }
  40%{
    color: rgb(12, 12, 12);    
  }
  50% {
    color: #fd2222;
  }
  100% {
    color: #ff0000;
  }
}

@keyframes colorchange {
  0% {
    color: rgb(12, 12, 12);
  }
  40%{
    color: rgb(12, 12, 12);    
  }
  50% {
    color: #fd2222;
  }
  100% {
    color: #ff0000;
  }
}
/* playing card setup */
.playingcard-box{
  background:black;
  -webkit-animation:1s ease-out 0s slideInFromLeft;
          animation:1s ease-out 0s slideInFromLeft;      
  border: 14px solid black;
  width: 80%;
  margin: 0 auto;
  border-radius: 4%;
  height: 178px;
  position: relative;
  overflow: hidden;
}
.chr-description{
  color:white;
  text-align: center;
  -webkit-animation:0.5s ease-in 0s fadeIn;
          animation:0.5s ease-in 0s fadeIn;
  
}
.thumbnail{
  -webkit-animation:0.5s ease-in 0s fadeIn;
          animation:0.5s ease-in 0s fadeIn;  
}
.character-name{
  margin:0;
  padding: 0;
  text-align: center;
  color:black;
  font-weight:bold;
  font-size: 20px;
  background: rgb(11, 243, 11);
}
.value{
  position: absolute;
  bottom:0;
  right:0;
  padding:8%;
  border-radius: 50%;
  font-weight: bold;
  font-size: 20px;
  color:rgb(11, 243, 11);
  background: black;
}
/* playing card classes toggleing animation */
.one{
  -webkit-animation:2s ease-in 0s fadeIn;
          animation:2s ease-in 0s fadeIn;
  border: 14px solid black;
  width: 80%;
  margin: 0 auto;
  border-radius: 4%;
  max-height: 100%;
  position: relative;
  overflow: hidden;
}
.two{
  -webkit-animation:3s ease-in 0s fadeIn;
          animation:3s ease-in 0s fadeIn;
  border: 14px solid black;
  width: 80%;
  margin: 0 auto;
  border-radius: 4%;
  max-height: 100%;  
  position: relative;
  overflow: hidden;
}
.three{
  -webkit-animation:2s ease-in 0s slideInFromLeft;
          animation:2s ease-in 0s slideInFromLeft;
  border: 14px solid black;
  width: 80%;
  margin: 0 auto;
  border-radius: 4%;
  max-height: 100%;
  position: relative;
  overflow: hidden;
}
@-webkit-keyframes fadeInOut{
  0% {
    opacity:1
  }
 50%{
   opacity:0.9

 }
  100%{
    opacity:1
  }
}
@keyframes fadeInOut{
  0% {
    opacity:1
  }
 50%{
   opacity:0.9

 }
  100%{
    opacity:1
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0.01;
  }
  100%{
    opacity:1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0.01;
  }
  100%{
    opacity:1;
  }
}
@-webkit-keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity:0.01;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity:1;
  }
}
@keyframes slideInFromLeft {
  0% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    opacity:0.01;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity:1;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.01;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0.01;
  }
}

/* end of playing card setup */
